}

.ol-control.ol-bar.ol-right {
    left: auto;
    right: 0.5em;
    top: 50%;
    -webkit-transform: translate(0px, -50%);
    transform: translate(0px, -50%);
}

.ol-control.ol-bar.ol-right .ol-control {
