    border-radius: 1em;
    display: block;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    border: 2px solid #ccc;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.ol-ext-toggle-switch:hover span {
    background-color: #999;
}

.ol-ext-toggle-switch:hover span:before {
    border-color: #999;
}

.ol-ext-toggle-switch input:checked+span {
    background-color: currentColor;
}

.ol-ext-toggle-switch input:checked+span:before {
    -webkit-transform: translate(.6em, -50%);
    transform: translate(.6em, -50%);
    border-color: currentColor;
}


/* Check/radio buttons */

.ol-ext-check {
    position: relative;
    display: inline-block;
}

.ol-ext-check input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.ol-ext-check span {
    color: rgba(0, 60, 136, .5);
    position: relative;
    display: inline-block;
    width: 1em;
    height: 1em;
    margin: -.2em .5em .2em;
    background-color: #ccc;
    vertical-align: middle;
}

.ol-ext-check:hover span {
    background-color: #999;
}

.ol-ext-check input:checked~span {
    background-color: currentColor;
}

.ol-ext-check input:checked~span:before {
    content: "";
    position: absolute;
    width: .5em;
    height: .8em;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-.1em) translate(-50%, -50%) rotate(45deg);
    transform: translateY(-.1em) translate(-50%, -50%) rotate(45deg);
    -webkit-box-shadow: inset -0.2em -0.2em #fff;
    box-shadow: inset -0.2em -0.2em #fff;
}

.ol-fixedoverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}